import ActionAreaCard from "./Card"
import styles from "./TrainerCard.module.css"
import trainer1 from "../../images/grid2.png"
import trainer2 from "../../images/grid3.png"
import trainer3 from "../../images/ .png"
import trainer4 from "../../images/ .png"
import { useEffect } from "react"
import ScrollReveal from "scrollreveal"
import Slider from "react-slick"
const TrainerCard=()=>{

    useEffect(()=>{
        ScrollReveal().reveal(`.${styles.title}`, {
            duration: 500,
            distance: "60px",
            origin: "top",
            easing: "ease-out",
            reset: false,
            viewFactor: 0.2,
            interval: 100,
            delay: 200,
            scale: 1,
          });
        },[])
        const sliderSettings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: window.innerWidth > 750 ? 2 : 1,
            slidesToScroll:  window.innerWidth> 750 ? 2 : 1,
          };
    return <>
    
    <div className={styles.title} id=" ">   </div>
    <div className={styles.row}>
    {/*    */}
    <div className={styles.sliderContainer}>
  <Slider {...sliderSettings} style={{zIndex:0}}>

  <ActionAreaCard src={trainer1} name=" " description="     ,   ,   " background="white"/>
    <ActionAreaCard src={trainer2} name=" " description="   ,  "  background="white"/>
   
    <ActionAreaCard src={trainer3} name="  " description="  
  ,    "  background="white"/>
 <ActionAreaCard src={trainer4} name=" " description="    ,      "  background="white"/>
  </Slider></div>
  
    </div>
    
    
    
    </>
}
export default TrainerCard