import styles from "./NewImageGrid.module.css"
import grid1 from "../../images/    .png"

import grid2 from "../../images/     1.png"
import grid3 from "../../images/     2.png"
import grid4 from "../../images/     3.png"
import grid5 from "../../images/     4.png"
import grid6 from "../../images/     5.png"
import grid7 from "../../images/     6.png"
import grid8 from "../../images/     7.png"
import grid13 from "../../images/     8.png"
 import grid10 from "../../images/     9.png"
 import grid14 from "../../images/     10.png"
 import grid12 from "../../images/     11.png"
//  import grid15 from "../../images/grid15.png"
import { useEffect, useState } from "react"
import ScrollReveal from "scrollreveal"
const NewImagesGrid=()=>{

const [zoom,setZoom]=useState(false)





    useEffect(()=>{
        ScrollReveal().reveal(`.${styles.image1}`, {
            duration: 600,
            distance: "60px",
            origin: "top",
            easing: "ease-out",
            reset: false,
            viewFactor: 0.2,
            interval: 300,
            delay: 200,
            scale: 1,
          });
          ScrollReveal().reveal(`.${styles.image2}`, {
            duration:600,
            distance: "60px",
            origin: "top",
            easing: "ease-out",
            reset: false,
            viewFactor: 0.2,
            interval: 300,
            delay: 200,
            scale: 1,
          });
          ScrollReveal().reveal(`.${styles.image3}`, {
            duration:600,
            distance: "60px",
            origin: "top",
            easing: "ease-out",
            reset: false,
            viewFactor: 0.2,
            interval: 300,
            delay: 200,
            scale: 1,
          });
    },[])
    return <>

    <div className={styles.container}>
    <div className={styles.row2}>
    <img className={styles.image3}  alt="   "src={grid7} />
    <img className={styles.image1}  alt="   "src={grid4} />
    <img className={styles.image3}  alt="   "src={grid1} />
    <img className={styles.image3}  alt="   "src={grid2} />
    <img className={styles.image3}  alt="   "src={grid3} />
    <img className={styles.image1}  alt="   "src={grid14} />
    </div>

    <div className={styles.row2}>
    <img className={styles.image3}  alt="   "src={grid10} />
    <img className={styles.image1}  alt="   "src={grid13} />
    <img className={styles.image3}  alt="   "src={grid8} />
    <img className={styles.image3}  alt="   "src={grid6} />
    <img className={styles.image3}  alt="   "src={grid12} />
    <img className={styles.image1}  alt="   "src={grid5} />
    </div>
    {/* <div className={styles.row}>
    <img className={styles.image3}  alt="   "src={image6}/>
    </div> */}
    {/* <div className={styles.row2}>


    </div> */}
    
    </div>
    </>
    }
    export default NewImagesGrid