import React from 'react';
import { Link as ScrollLink } from "react-scroll"
import styles from './NavBar.module.css'; // Import your CSS module
import Button from './Button';
import logo from "../images/ .png"
import Button2 from './Button2';
const StickyNavbar = () => {
  return <>
       {window.innerWidth < 850 && <div className={styles.smallNav}><Button2 text=" " msg="        "/>
       {window.innerWidth >= 550 && window.innerWidth<=850 && <img className={styles.logo} src={logo} alt=" "/> }</div>}       
       {window.innerWidth > 850 &&  <div className={styles.stickyNavbar}>
       <div className={styles.navLogo}><Button msg="        "/></div>
       <ul className={styles.navLinks}>
   

    <ScrollLink to=" " smooth={true} duration={1500} offset={-100} className={styles.navLink}> </ScrollLink>
     <ScrollLink to="" smooth={true} duration={1500}  offset={-100} className={styles.navLink}></ScrollLink>
     <ScrollLink to=" " smooth={true} duration={1500}  offset={-100} className={styles.navLink}> </ScrollLink>
     <ScrollLink to=" " smooth={true} duration={1500} offset={-100} className={styles.navLink}> </ScrollLink>
   </ul>
 </div>}

</>
};

export default StickyNavbar;